body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

*,*::before,*::after{box-sizing:border-box}.App{text-align:center}.page{overflow:hidden;display:flex;flex-direction:column;min-height:100vh}.containerContent{padding-top:80px}.main{padding:10px;margin-top:24px;margin-bottom:18px;background-color:lightyellow}

.carousel-indicators li{background-color:blue}.carousel-caption{top:60px;left:40px;right:40px}.carousel-caption h1{text-transform:uppercase;font-family:'Yanone Kaffeesatz', sans-serif;font-weight:400;font-size:2rem;text-align:right}


.foot{margin-top:auto}.navbarWrapper{display:flex;flex-direction:row;justify-content:space-around;align-items:center;color:lightskyblue}.footerText{font-size:14px;padding-left:12px}.footer-logo:hover{-webkit-animation:footer-logo-spin infinite 10s linear;animation:footer-logo-spin infinite 10s linear}@-webkit-keyframes footer-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes footer-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}

